import React from 'react';
import "./Card.scss"

const Card = ({title, description, icon, translationKey}) =>
    <div id="Card">
        <div className="cardHeader">
            <img className="cardIcon" src={icon} alt={title}/>

            <h2 id="title" ling-translate={translationKey}>{title}</h2>
        </div>
        <p id="description"  ling-translate={translationKey + "Text"}>{description}</p>
        <div className="orangeBar" />
    </div>;

export default Card;